.photo {
    width: 100%;
    min-height: 100vh;
    position: relative;
    &__title {
        width: 100%;
        height: auto;
        padding-left: 210px;
        padding-top: 100px;
        margin-bottom: 75px;
        position: relative;
        z-index: 2;
        p {
            font-size: 50px;
            font-weight: 600;
            text-transform: uppercase;
            font-family: 'Oswald', sans-serif;
            color: #161616; } }
    .grid {
        width: 100%;
        padding-left: 210px;
        padding-right: 100px;
        position: relative;
        z-index: 2;
        .grid-item {
            width: 25%;
            margin: 0 50px 50px 0;
            img {
                width: 100%;
                transition: .3s; }
            &__title {
                p {
                    padding-top: 10px;
                    font-family: 'Oswald', sans-serif;
                    color: #161616;
                    line-height: 18.5px;
                    letter-spacing: 2px;
                    text-transform: uppercase;
                    margin: 0; } }
            &__read_more {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                height: auto;
                position: relative;
                padding-top: 10px;
                margin-bottom: 15px;
                p {
                    display: inline;
                    font-family: 'Oswald', sans-serif;
                    margin: 0;
                    font-size: 9px;
                    text-transform: uppercase;
                    line-height: 18.5px;
                    letter-spacing: 2px;
                    position: relative;
                    color: #161616; }
                &__line {
                    height: 1px;
                    position: relative;
                    margin-left: 5px;
                    width: 20px;
                    background-color: #161616;
                    transition: .3s;
                    &:before {
                        content: '';
                        width: 5px;
                        height: 1px;
                        position: absolute;
                        background-color: #161616;
                        right: 0;
                        top: -1px;
                        transform: rotate(30deg); }
                    &:after {
                        content: '';
                        width: 5px;
                        height: 1px;
                        position: absolute;
                        background-color: #161616;
                        right: 0;
                        top: 1px;
                        transform: rotate(-30deg); } } }
            &:hover {
                text-decoration: none;
                img {
                    box-shadow: 0 24px 65px -20px rgba(0,0,0,.85);
                    transform: scaleY(0.99);
                    transform-origin: top; }
                .grid-item__read_more__line {
                    margin-left: 15px; }
                .news .grid .grid-item__title p {
                    transform: scaleY(1.1); } } } } }
@media all and (max-width: 1850px) {
    .photo {
        .grid {
            .grid-item {
                width: 20%; } } } }
@media all and (max-width: 1250px) {
    .photo {
        .grid {
            padding: 25px 15px;
            .grid-item {
                width: 40%; } } } }
@media all and (max-width: 1250px) {
    .photo {
        &__title {
            padding-left: 15px;
            padding-top: 50px;
            margin-bottom: 50px; }
        .grid {
            .grid-item {
                width: 41%; } } } }
@media all and (max-width: 600px) {
    .photo {
        &__title {
            padding-left: 15px;
            padding-top: 50px;
            margin-bottom: 0; }
        .grid {
            .grid-item {
                width: 92%; } } } }
