@keyframes fadeToTop {
    0% {
        opacity: 0;
        transform: translateY(50px); }
    100% {
        opacity: 1;
        transform: translateY(0); } }
.contacts {
    width: 100%;
    height: 100vh;
    padding-top: 150px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    padding-right: 205px;
    background-color: #eee;
    &__title {
        width: 50%;
        height: auto;
        padding-left: 210px;
        margin-bottom: 90px;
        opacity: 0;
        transform: translateY(50px);
        animation: fadeToTop 0.8s ease-out;
        animation-fill-mode: forwards;
        animation-delay: 0.5s;
        position: relative;
        z-index: 2;
        a {
            font-family: 'Oswald', sans-serif;
            text-transform: none;
            color: #999;
            font-size: 18px;
            line-height: 30px;
            letter-spacing: 2px;
            display: inline-block;
            &:nth-child(2) {
                text-transform: none;
                color: #666;
                font-size: 30px;
                line-height: 30px;
                margin-top: 75px;
                font-weight: 400; }
            &:hover {
                color: #161616;
                text-decoration: none; } }
        p {
            font-size: 50px;
            text-transform: uppercase;
            font-family: 'Oswald', sans-serif;
            font-weight: 600;
            color: #161616; } }

    &__form {
        height: 560px;
        width: 815px;
        position: relative;
        z-index: 2;
        right: 0;
        background-color: #161616;
        padding: 90px;
        font-family: 'Oswald', sans-serif;
        color: #999;
        opacity: 0;
        transform: translateY(50px);
        font-size: 12px;
        animation: fadeToTop 0.5s ease-out;
        animation-fill-mode: forwards;
        animation-delay: 0.75s;
        label {
            width: calc(50% - 110px);
            text-transform: uppercase;
            position: absolute;
            letter-spacing: 2px;
            &:nth-of-type(2) {
                right: 90px; }
            &:nth-of-type(3) {
                top: 175px;
                width: calc(100% - 180px); }
            input {
                font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
                font-size: 11px;
                font-weight: 400;
                color: rgb(153, 153, 153);
                background-color: #161616;
                outline: none;
                line-height: 30px;
                width: 100%;
                border: none;
                border-bottom: 1px solid #999;
                padding: 0 0 0 5px; }
            textarea {
                font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
                font-size: 11px;
                font-weight: 400;
                color: rgb(153, 153, 153);
                min-width: 100%;
                max-width: 100%;
                margin-top: 15px;
                border: none;
                max-height: 205px;
                min-height: 205px;
                background-color: #161616;
                outline: none;
                &::-webkit-scrollbar {
                    width: 5px; }
                &::-webkit-scrollbar-thumb {
                    border-width: 1px 1px 1px 2px;
                    border-color: #777;
                    border-radius: 4px;
                    background-color: #666; }
                &::-webkit-scrollbar-track {
                    border-width: 1px solid #fff; }
                &:before {
                    content: 'asdfasdf'; } }
            .back_form {
                position: absolute;
                top: 70px;
                left: 0;
                width: calc(100% - 12px);
                height: 100%;
                background-image: url(../img/contacts/back_form.png);
                background-repeat: no-repeat; } }
        .submit_btn {
            transition: .3s;
            width: 280px;
            height: 70px;
            position: absolute;
            bottom: 70px;
            background-color: #eee;
            font-size: 12px;
            text-transform: uppercase;
            color: #161616;
            border: 1px solid #eee;
            letter-spacing: 2px;
            &:hover {
                color: #eee;
                background-color: #161616; } } } }
@media all and (max-width: 1500px) {
    .contacts {
        padding-right: 0; } }
@media all and (max-width: 1250px) {
    .contacts {
        padding-right: 15px;
        &__title {
            padding-left: 15px; } } }
@media all and (max-width: 1024px) {
    .contacts {
        padding-left: 15px;
        flex-direction: column;
        justify-content: flex-start;
        &__title {
            padding-left: 0; }
        &__form {
            width: 100%;
            padding: 25px 15px;
            label {
                width: 100%;
                position: relative;
                &:nth-of-type(2) {
                    right: 0;
                    margin-top: 25px; }
                &:nth-of-type(3) {
                    top: 0;
                    margin-top: 25px;
                    width: 100%; } }
            .submit_btn {
                width: 100%;
                margin-top: 25px;
                position: relative;
                bottom: 0; } } } }
@media all and (max-width: 600px) {
    .contacts {
        padding-top: 50px;
        height: auto;
        padding-bottom: 15px;
        &__title {
            width: 100%;
            margin-bottom: 50px;
            p {
                font-size: 40px; }
            a:nth-child(2) {
                font-size: 22px;
                margin-top: 0; } }
        &__form {
            padding: 25px 15px;
            label {
                width: 100%;
                position: relative;
                &:nth-of-type(1) {
                    right: 0;
                    margin-top: 25px; }
                &:nth-of-type(2) {
                    top: 0;
                    margin-top: 25px;
                    width: 100%; } }
            .submit_btn {
                width: 100%;
                margin-top: 25px;
                position: relative;
                bottom: 0; } } } }
@media all and (max-width: 350px) {
    .contacts__title {} }
