.photo_page {
    height: 100vh;
    .photo_open {
        z-index: 2;
        position: relative;
        &__main {
            display: flex;
            justify-content: space-between;
            width: 100%;
            padding-left: 210px;
            padding-right: 185px;
            height: calc(100vh - 346px);
            position: relative; }
        &__left {
            width: 50%;
            background-color: #161616;
            overflow: hidden;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            img {
                position: absolute;
                width: 100%;
                opacity: 0;
                transition: .5s;
                transform: scale(1.2); }
            .active_photo {
                opacity: 1;
                transform: scale(1); } }
        &__right {
            width: 50%;
            background-color: #161616;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            img {
                position: absolute;
                width: 300px;
                opacity: 0;
                transition: .5s;
                transform: scale(0.8); }
            .active_photo {
                opacity: 1;
                transform: scale(1); } }
        &__nav {
            position: absolute;
            top: 0;
            left: calc(50% - 37px);
            width: 100px;
            height: 50px;
            background-color: #161616;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            z-index: 2;
            .nav_back {
                transition: .3s;
                width: 50%;
                height: 100%;
                text-align: center;
                padding-top: 15px;
                cursor: pointer;
                background-image: url(../img/press/prev.png);
                background-position: center;
                background-repeat: no-repeat;
                &:hover {
                    background-position-x: 10px;
                    padding-right: 10px; } }
            .nav_next {
                transition: .3s;
                width: 50%;
                height: 100%;
                text-align: center;
                padding-top: 15px;
                cursor: pointer;
                background-image: url(../img/press/next.png);
                background-position: center;
                background-repeat: no-repeat;
                &:hover {
                    background-position-x: 25px;
                    padding-left: 10px; } } } } }
@media all and (max-width: 1250px) {
    .photo_page {
        .photo_open__main {
            padding: 0;
            height: calc(100vh - 171px); } } }
@media all and (max-width: 1024px) {
    .photo_page {
        .photo_open {
            &__main {
                flex-direction: column; }
            &__left {
                width: 100%;
                height: 50%; }
            &__right {
                width: 100%;
                height: 50%;
                img {
                    max-height: 100%;
                    width: auto; } } } } }
@media all and (max-width: 600px) {
    .photo_page {
        .photo_open {
            &__main {
                height: calc(100vh - 131px); } } }
    .photo_page .photo_open__nav {
        left: auto;
        right: 0;
        top: -50px;
        .nav_back, .nav_next {
            &:hover {
                padding: 0;
                background-position: center; } } } }
