.video {
    width: 100%;
    background-color: #eee;
    min-height: 100vh;
    &__title {
        width: 100%;
        height: auto;
        padding-left: 210px;
        padding-top: 100px;
        margin-bottom: 75px;
        position: relative;
        z-index: 2;
        p {
            font-size: 50px;
            text-transform: uppercase;
            font-family: 'Oswald', sans-serif;
            font-weight: 600;
            color: #161616;
            z-index: 2; } }
    &__items {
        width: calc(100% - 70px);
        padding-left: 210px;
        padding-right: 100px;
        display: flex;
        position: relative;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
        z-index: 2;
        &__item {
            width: 30%;
            overflow: hidden;
            margin: 0 50px 50px 0;
            position: relative;
            img {
                width: 100%; }
            &:before {
                transition: .3s;
                content: url('../img/video/play_icon.png');
                position: absolute;
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                opacity: 0.6; }
            &__title {
                p {
                    padding-top: 10px;
                    font-family: 'Oswald', sans-serif;
                    color: #161616;
                    line-height: 18.5px;
                    letter-spacing: 2px;
                    text-transform: uppercase;
                    margin: 0; } }
            &:hover {
                text-decoration: none;
                &:before {
                    opacity: 1; } } } } }
@media all and (max-width: 1250px) {
    .video {
        &__title {
            padding-top: 50px;
            margin-bottom: 50px;
            padding-left: 15px; }
        &__items {
            width: 100%;
            padding: 25px 15px; } } }
@media all and (max-width: 768px) {
    .video__items__item {
        width: calc(50% - 15px);
        margin-right: 15px; } }
@media all and (max-width: 600px) {
    .video {
        &__title {
            margin-bottom: 0; } }
    .video__items__item {
        width: 100%;
        margin-right: 0; } }
