@keyframes slideTopLetter {
    0% {
        opacity: 0;
        top: -250px; }
    50% {
        top: 105px;
        opacity: 0.5; }
    100% {
        top: 50px;
        opacity: 1; } }
@keyframes scrollDown {
    0% {
        opacity: 0;
        margin-bottom: 50px; }
    50% {
        opacity: 1;
        margin-bottom: 0; }
    100% {
        opacity: 0;
        margin-bottom: 50px; } }
@keyframes fadeDown {
    0% {
        opacity: 0;
        transform: translateY(-100%); }
    100% {
        opacity: 1;
        transform: translateY(0); } }
@keyframes slideLeft {
    0% {
        opacity: 0;
        left: -100px; }
    50% {
        opacity: .5; }
    100% {
        opacity: 1;
        left: 0; } }
@keyframes slideRight {
    0% {
        opacity: 0;
        left: 100px; }
    50% {
        opacity: .5; }
    100% {
        opacity: 1;
        left: 0; } }
@keyframes slideBottom {
    0% {
        opacity: 0;
        top: 150px; }
    50% {
        opacity: .5; }
    100% {
        opacity: 1;
        top: 50px; } }
@keyframes fadeOut {
    0% {
        opacity: 1; }
    100% {
        opacity: 0;
        z-index: -500; } }
body {
    background-color: #161616; }
.overlay {
    content: '';
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    height: 100%;
    background-color: #eee;
    animation: fadeOut 0.75s ease-in-out;
    animation-fill-mode: forwards;
    animation-delay: 0.2s;
    z-index: 555555; }
.intro {
    width: 100%;
    height: 100vh;
    background-image: url(../img/intro/main_back.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100% auto;
    &:before {
        content: '';
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        height: 100%;
        background-color: rgba(99, 96, 96, 0.3);
        z-index: 0; }

    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-image: url(../img/intro/main_back.jpg);
        background-size: auto;
        background-position: center;
        background-repeat: no-repeat;
        mask: url(../img/intro/mask3.svg) center / contain no-repeat;
        background-size: 100%;
        mask-size: 100%;
        z-index: 5;
        &:before {
            content: '';
            position: absolute;
            width: 100%;
            top: 0;
            left: 0;
            height: 100%;
            background-color: rgba(99, 96, 96, 0.3);
            z-index: 0; } }
    &__logo {
        position: absolute;
        right: 60px;
        top: 50px;
        z-index: 1;
        opacity: 0;
        transform: translateY(-100%);
        animation: fadeDown 1s linear;
        animation-fill-mode: forwards;
        animation-delay: 4s; }
    &__content {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        &__title {
            z-index: 1;
            display: flex;
            flex-direction: row;
            align-items: center;
            overflow: hidden;
            height: 600px;
            &__letter {
                width: 110px;
                height: 120px;
                background-image: url(../img/intro/main_title.png);
                background-position: 0 0;
                position: relative;
                top: -250px;
                animation: slideTopLetter 3s ease-in-out;
                animation-fill-mode: forwards;
                animation-delay: 1s;
                opacity: 0;
                &:nth-child(2) {
                    animation: slideTopLetter 2s ease-in-out;
                    animation-fill-mode: forwards;
                    background-position: -115px;
                    animation-delay: 1.1s; }
                &:nth-child(3) {
                    animation: slideBottom 1.9s ease-in-out;
                    animation-fill-mode: forwards;
                    background-position: -220px;
                    top: 150px;
                    animation-delay: 1.2s; }
                &:nth-child(4) {
                    animation: slideTopLetter 2.5s ease-in-out;
                    animation-fill-mode: forwards;
                    background-position: -320px;
                    animation-delay: 1.3s; }
                &:nth-child(5) {
                    animation: slideTopLetter 4.5s ease-in-out;
                    animation-fill-mode: forwards;
                    background-position: -420px;
                    animation-delay: 1.4s; }
                &:nth-child(6) {
                    animation: slideTopLetter 1.5s ease-in-out;
                    animation-fill-mode: forwards;
                    background-position: -515px;
                    animation-delay: 1.5s; }
                &:nth-child(7) {
                    animation: slideTopLetter 2.8s ease-in-out;
                    animation-fill-mode: forwards;
                    background-position: -600px;
                    animation-delay: 1.6s; }
                &:nth-child(8) {
                    animation: slideTopLetter 1.7s ease-in-out;
                    animation-fill-mode: forwards;
                    background-position: -710px;
                    animation-delay: 1.7s; }
                &:nth-child(9) {
                    animation: slideTopLetter 4s ease-in-out;
                    animation-fill-mode: forwards;
                    background-position: -820px;
                    animation-delay: 1.8s; }
                &:nth-child(10) {
                    animation: slideTopLetter 1.3s ease-in-out;
                    animation-fill-mode: forwards;
                    background-position: -925px;
                    animation-delay: 1.9s; } } }
        &__scroll {
            position: absolute;
            bottom: 125px;
            transform: rotate(-90deg);
            z-index: 6;
            animation: scrollDown 3s linear;
            animation-delay: 4s;
            animation-fill-mode: forwards;
            animation-iteration-count: infinite;
            margin-bottom: 50px;
            opacity: 0;
            p {
                color: #fff;
                margin: 0;
                font-size: 0.8em;
                text-transform: uppercase;
                letter-spacing: 2px;
                font-family: 'Oswald', sans-serif; }
            &:after {
                content: '';
                position: absolute;
                top: calc(50% - 0.6px);
                left: -60%;
                width: 30px;
                height: 1.2px;
                background-color: #bbb; } } } }
@media all and (max-width: 1400px) {
    .intro__content__title__letter:nth-child(4) {
        margin-left: 10px; } }
@media all and (max-width: 1350px) {
    .intro {
        background-size: cover; }
    .intro__overlay {
