.theme_btn {
    position: fixed;
    z-index: 9999;
    bottom: 35px;
    left: 30px;
    outline: none;
    height: 20px; }
.theme_btn > label {
    top: 0;
    margin-top: 0;
    position: relative;
    padding-left: 45px;
    outline: none; }
.theme_btn > label > input {
    width: 35px;
    height: 20px;
    border-radius: 10px;
    position: absolute;
    -webkit-appearance: none;
    position: absolute;
    top: 0;
    left: 0;
    margin-left: 3px;
    border: 1px solid #ccc;
    outline: none !important; }
.theme_btn > label > input:checked {
    outline: none;
    // background: #161616
    border-color: #fff; }
.theme_btn > label > input:before {
    content: "";
    letter-spacing: 1px;
    color: rgba(0,0,0,.15);
    font-size: 22px;
    font-weight: 100;
    text-shadow: 1px 1px 1px rgba(255,255,255,1);
    width: 14px;
    height: 14px;
    padding: 0;
    top: 2px;
    left: 2px;
    position: absolute;
    border-radius: 40px;
    background: #161616;
    -webkit-transition: all 0.3s; }
.theme_btn > label > input:checked:before {
	left: 18px;
	background: #fff; }
.theme_black {
    background-position: -23px 0; }
@media all and (max-width: 1250px) {
    .theme_btn {
        top: 5px;
        right: 15px;
        left: auto; } }
