@keyframes fadeIn {
    0% {
        opacity: 0; }
    100% {
        opacity: 1; } }
@keyframes fadeInTop {
    0% {
        opacity: 0;
        transform: translateY(50px); }
    100% {
        opacity: 1;
        transform: translateY(0); } }
.about {
    width: 100%;
    display: flex;
    flex-direction: row;
    position: relative;
    &__left {
        width: 50%;
        padding-top: 180px;
        padding-left: 265px;
        position: relative;
        padding-bottom: 150px;
        z-index: 2;
        &__title {
            p {
                font-family: 'Oswald', sans-serif;
                color: #161616;
                font-size: 40px;
                text-transform: uppercase;
                line-height: 50px;
                span {
                    font-size: 50px; } } }
        &__info {
            width: 70%;
            p {
                line-height: 30px;
                color: #999; } }
        &__social {
            width: calc(100% - 115px);
            position: absolute;
            bottom: 50px;
            left: 115px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            z-index: 5;
            p {
                color: #161616;
                font-family: 'Oswald', sans-serif;
                text-transform: uppercase;
                letter-spacing: 2px; }
            &__links {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                a {
                    width: 30px;
                    height: 30px;
                    display: block;
                    margin: 0 20px;
                    background-image: url(../img/header/social_dark.png);
                    background-position: -2px -2px;
                    transform: scale(1.2);
                    &:first-child {
                        background-position: -72px -2px; }
                    &:nth-child(2) {
                        background-position: -37px -2px; } } }
            &__lines {
                position: absolute;
                width: 100%;
                height: 150%;
                top: -25%;
                left: 0;
                z-index: -1;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                &__line_main {
                    width: 100%;
                    height: 1px;
                    position: absolute;
                    top: 40%;
                    background-color: #ccc; }
                img {
                    position: relative;
                    top: -10px;
                    max-width: 100%; } } } }
    &_wrapper {
        height: 100%;
        width: 50%;
        z-index: 2; }
    &__right {
        width: 100%;
        height: 100vh;
        overflow: hidden;
        position: relative;
        z-index: 2;
        img {
            max-width: 100%;
            width: 100%;
            animation: fadeIn 1s ease-in-out;
            opacity: 0;
            animation-delay: 0.5s;
            animation-fill-mode: forwards; } }
    &__quote {
        position: absolute;
        z-index: 2;
        left: 45%;
        top: 35%;
        width: 480px;
        height: 240px;
        background-color: #161616;
        display: flex;
        align-items: center;
        opacity: 0;
        transform: translateY(50px);
        padding: 30px;
        animation: fadeInTop 1s ease-in-out;
        animation-delay: 1s;
        animation-fill-mode: forwards;
        p {
            color: #999;
            position: relative;
            &:before {
                content: url(../img/about/quote_top.png);
                position: absolute;
                top: -60px;
                left: 0; }
            &:after {
                content: url(../img/about/quote_top.png);
                position: absolute;
                bottom: -80px;
                right: 0; } }
        &__twits {
            position: absolute;
            left: 30px;
            bottom: 30px;
            img {
                &:first-child {
                    margin-right: 15px; } } } } }

@media all and (max-width: 1400px) {
    .about {
        &__left {
            padding-left: 165px;
            padding-top: 80px;
            &__info {
                width: calc(100% - 40px); } }
        &__quote {
            left: calc(50% - 15px); } } }
@media all and (max-width: 1250px) {
    .about {
        padding: 0;
        min-height: 100vh;
        flex-direction: column-reverse;
        align-items: flex-end;
        justify-content: flex-end;
        &__left {
            width: 100%;
            padding: 0 15px;
            min-height: calc(50vh + 50px);
            position: relative;
            top: -50px;
            z-index: 99;
            padding-bottom: 130px;
            &__title {
                p {
                    color: #fff;
                    span {
                        color: #161616; } } }
            &__social {
                left: 0;
                bottom: 25px;
                width: 100%; }
            &__info {
                width: 100%;
                margin-top: 0; } }
        &_wrapper {
            width: 100%;
            min-height: 50vh !important;
            max-height: 50vh;
            position: relative;
            top: 0 !important; }
        &__right {
            width: 100%;
            height: 100%; }
        &__quote {
            display: none; } } }
