.press {
    width: 100%;
    height: 100vh;
    &__title {
        width: 100%;
        height: auto;
        padding-left: 210px;
        margin-bottom: 90px;
        padding-top: 100px;
        position: relative;
        z-index: 2;
        p {
            font-size: 50px;
            text-transform: uppercase;
            font-family: 'Oswald', sans-serif;
            font-weight: 600;
            color: #161616; } }
    &__items {
        width: 100%;
        max-height: calc(100vh - 261px);
        padding-left: 115px;
        &__item {
            width: 100%;
            display: flex;
            max-height: calc(100vh - 261px);
            flex-direction: row;
            flex-wrap: nowrap;
            &__photo {
                width: 50%;
                height: calc(100vh - 261px);
                overflow: hidden;
                position: relative;
                .owl-stage-outer {
                    overflow: hidden;
                    .owl-stage {
                        display: flex;
                        flex-direction: row;
                        justify-content: flex-start; } }
                .owl-nav {
                    position: absolute;
                    z-index: 2;
                    top: 0;
                    left: calc(50% - 50px);
                    width: 100px;
                    height: 50px;
                    background-color: #161616;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    align-items: center;
                    .owl-prev {
                        transition: .3s;
                        width: 50%;
                        height: 100%;
                        text-align: center;
                        padding-top: 15px;
                        cursor: pointer;
                        background-image: url(../img/press/prev.png);
                        background-position: center;
                        background-repeat: no-repeat;
                        &:hover {
                            background-position-x: 10px;
                            padding-right: 10px; } }
                    .owl-next {
                        transition: .3s;
                        width: 50%;
                        height: 100%;
                        text-align: center;
                        padding-top: 15px;
                        cursor: pointer;
                        background-image: url(../img/press/next.png);
                        background-position: center;
                        background-repeat: no-repeat;
                        &:hover {
                            background-position-x: 25px;
                            padding-left: 10px; } } }
                img {
                    width: 100%; } }
            &__desc {
                position: relative;
                z-index: 2;
                width: 50%;
                top: -261px;
                bottom: 0;
                height: 100vh;
                max-height: 100vh;
                overflow-y: scroll;
                background-color: #161616;
                padding: 55px;
                .press_prev_item {
                    position: fixed;
                    display: inline-flex;
                    justify-content: flex-start;
                    align-items: center;
                    height: 100px;
                    width: calc(50% - 80px);
                    left: calc(50% + 60px);
                    top: 0;
                    margin: 0;
                    padding: 0;
                    padding-left: 55px;
                    background-color: #161616;
                    a {
                        transition: .3s;
                        margin: 0;
                        padding: 0;
                        color: #ccc;
                        letter-spacing: 2px;
                        line-height: 36px;
                        font-size: 12px;
                        font-family: 'Oswald', sans-serif;
                        text-transform: uppercase;
                        &:hover {
                            text-decoration: none;
                            color: #fff;
                            cursor: pointer; } } }
                .press_next_item {
                    position: fixed;
                    display: inline-flex;
                    justify-content: flex-end;
                    align-items: center;
                    height: 100px;
                    top: 0;
                    text-align: right;
                    right: 80px;
                    margin: 0;
                    padding: 0;
                    a {
                        transition: .3s;
                        margin: 0;
                        padding: 0;
                        color: #ccc;
                        letter-spacing: 2px;
                        line-height: 36px;
                        font-size: 12px;
                        font-family: 'Oswald', sans-serif;
                        text-transform: uppercase;
                        &:hover {
                            text-decoration: none;
                            color: #fff;
                            cursor: pointer; } } }
                p {
                    margin: 0;
                    font-family: 'Arial', sans-serif;
                    text-align: justify;
                    line-height: 30px;
                    font-size: 14px;
                    color: #999;
                    &:nth-of-type(1) {
                        margin: 70px 0 10px 0;
                        font-size: 30px;
                        line-height: 36px;
                        font-family: 'Oswald', sans-serif;
                        color: #fff; }
                    &:nth-of-type(2) {
                        font-size: 12px;
                        letter-spacing: 2px;
                        line-height: 30px;
                        color: #999;
                        margin: 0 0 35px 0;
                        font-family: 'Oswald', sans-serif; } }
                .press_info {
                    margin: 25px 0;
                    padding: 20px 75px 20px 20px;
                    border-left: 2px solid #fff; } } } } }
@media all and (max-width: 1250px) {
    .press {
        height: auto;
        &__title {
            padding-top: 50px;
            padding-left: 15px;
            margin-bottom: 50px; }
        &__items {
            padding-left: 0;
            &__item {
                max-height: none;
                flex-direction: column;
                &__photo {
                    width: 100%;
                    min-height: 60vh;
                    max-height: 60vh;
                    position: relative; }
                &__desc {
                    top: 0;
                    height: auto;
                    width: 100%;
                    max-height: none;
                    padding-bottom: 50px;
                    .press_prev_item, .press_next_item {
                        display: inline-block;
                        width: 49%;
                        height: 50px;
                        position: relative;
                        left: 0; }
                    p {
                        &:nth-child(3) {
                            margin-top: 25px; } } } } } } }
@media all and (max-width: 600px) {
    .press {
        &__items {
            &__item {
                &__photo {
                    min-height: 50vh;
                    max-height: 50vh;
                    .owl-nav {
                        .owl-prev, .owl-next {
                            &:hover {
                                background-position: center;
                                padding: 0; } } } }
                &__desc {
                    padding: 25px 15px;
                    .press_info {
                        padding-right: 30px; }
                    .press_prev_item, .press_next_item {
                        padding: 0; } } } } } }
