@keyframes height_100 {
    0% {
        height: 0;
        opacity: 0;
        background-color: #fff; }
    50% {
        height: 50vh;
        opacity: 0.5;
        background-color: #ccc; }
    100% {
        height: 100vh;
        opacity: 1;
        background-color: #999; } }
@keyframes height_100_2 {
    0% {
        height: 0;
        opacity: 0;
        background-color: #fff; }
    50% {
        height: 50vh;
        opacity: 0.5;
        background-color: #ab2f32; }
    100% {
        height: 100vh;
        opacity: 1;
        background-color: #777; } }
@keyframes opacity_100 {
    0% {
        opacity: 0; }
    100% {
        opacity: 1; } }
.header {
    width: 115px;
    height: 100vh;
    position: fixed;
    z-index: 999;
    .hamburger {
        display: none; }
    .logo_mob {
        display: none; }
    &:before {
        position: absolute;
        content: '';
        top: 0;
        right: -1px;
        width: 1.4px;
        opacity: 0;
        animation: height_100 1s linear;
        animation-fill-mode: forwards;
        animation-delay: 2s;
        height: 0;
        background-color: transparent; }
    ul {
        position: relative;
        top: 80px;
        width: 100%;
        transform: rotate(-90deg);
        display: flex;
        flex-direction: row-reverse;
        li {
            height: 40px;
            display: flex;
            align-items: center;
            position: relative;
            text-align: center;
            opacity: 0;
            animation: opacity_100 1s linear;
            animation-fill-mode: forwards;
            animation-delay: 2s;
            &:nth-child(2) {
                animation-delay: 2.15s; }
            &:nth-child(3) {
                animation-delay: 2.3s; }
            &:nth-child(4) {
                animation-delay: 2.45s; }
            &:nth-child(5) {
                animation-delay: 2.6s; }
            &:nth-child(6) {
                animation-delay: 2.75s; }
            &:nth-child(7) {
                animation-delay: 2.9s; }
            a {
                transition: color .3s, opacity .3s;
                text-align: center;
                text-transform: uppercase;
                color: #737373;
                font-size: 0.72em;
                letter-spacing: 4px;
                &:active, &:focus {
                    background-color: transparent; } }
            .bottom_line {
                transition: .3s;
                position: absolute;
                width: 1px;
                height: 0;
                background-color: #bbb;
                bottom: -25px;
                left: 50%; }
            .bottom_point {
                transition: .5s;
                position: absolute;
                content: '';
                width: 5px;
                height: 5px;
                background-color: transparent;
                bottom: -40px;
                left: calc(50% - 2px); } }

        .active {
            .bottom_line {
                height: 20px; }
            .bottom_point {
                background-color: #bbb; }
            a {
                color: #fff; } }
        .active_hover {
            .bottom_line {
                height: 20px; }
            .bottom_point {
                background-color: #bbb; } } }
    a.social_icons {
        animation: opacity_100 1s linear;
        animation-fill-mode: forwards;
        animation-delay: 2.5s;
        opacity: 0;
        display: block;
        width: 25px;
        height: 25px;
        background-image: url(../img/header/social.png);
        background-position: -5px -9px;
        position: absolute;
        left: 40px;
        bottom: 100px;
        transition: .3s;
        &:nth-child(3) {
            bottom: 135px;
            background-position: -44px -9px; }
        &:nth-child(2) {
            bottom: 170px;
            background-position: -80px -9px; }
        &:hover {
            transform: scale(1.2); } } }
.header_not-main {
    &:before {
        animation-delay: 0s;
        animation: height_100_2 1s linear;
        animation-fill-mode: forwards; }
    ul {
        li {
            animation-delay: 0.5s;
            a:hover {
                color: #fff !important; }
            &:nth-child(2) {
                animation-delay: 0.65s; }
            &:nth-child(3) {
                animation-delay: 0.8s; }
            &:nth-child(4) {
                animation-delay: 0.95s; }
            &:nth-child(5) {
                animation-delay: 1.1s; }
            &:nth-child(6) {
                animation-delay: 1.25s; }
            &:nth-child(7) {
                animation-delay: 1.4s; } }
        .active {
            .bottom_line {
                height: 20px;
                background-color: #161616; }
            .bottom_point {
                background-color: #161616; }
            a {
                color: #161616; } } }
    a.social_icons {
        background-image: url(../img/header/social_dark_sm.png);
        background-position: -5px -5px;
        &:nth-child(3) {
            bottom: 135px;
            background-position: -40px -5px; }
        &:nth-child(2) {
            bottom: 170px;
            background-position: -75px -5px; } } }
.header_not-main {
    .hamburger {
        filter: invert(1); }
    .hamb_left {
        filter: invert(0); } }
@media all and (min-width: 1200px) {
    .header {
        ul {
            li {
                a {
                    &:hover {
                        background-color: transparent;
                        color: #fff; } } } } }
    .header_not-main {
        ul {
            li {
                animation-delay: 0.5s;
                a:hover {
                    color: #ccc !important; } } } } }
@media all and (max-width: 1400px) {
    .header, .header_not-main {
        ul {
            top: 50px; }
        a.social_icons {
            bottom: 15px;
            &:nth-child(2) {
                bottom: 75px; }
            &:nth-child(3) {
                bottom: 45px; } } } }
@media all and (max-width: 1250px) {
    .header, .header_not-main {
        .logo_mob {
            transition: .3s;
            display: block;
            transform: translateX(-100%);
            z-index: 999999;
            text-align: center;
            width: 100vw;
            position: absolute;
            left: 0;
            top: 100px;
            img {
                width: 418px;
                filter: invert(1); } }
        &:before {
            display: none; }
        .hamburger {
            transition: .3s;
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            outline: none; }
        ul {
            top: 0;
            left: 0;
            padding-top: 200px;
            transition: .3s;
            position: absolute;
            transform: translateX(-100%);
            background-color: #fff;
            height: 100vh;
            width: 100vw;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            padding-bottom: 120px;
            li {
                color: #161616;
                font-size: 36px;
                margin: 15px 0; }
            .active {
                a {
                    color: #000; } } }
        .social {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            bottom: 75px;
            left: 0;
            width: 100vw;
            height: 35px;
            transition: .3s;
            transform: translateX(-100%);
            .social_icons {
                bottom: 0!important;
                left: 0 !important;
                display: inline-block;
                position: relative;
                margin: 0 25px;
                transform: scale(2);
                filter: invert(1); } }
        .hamb_left {
            left: calc(100vw - 65px); }
        .active_mob {
            transform: translateX(0); } } }
@media all and (max-width: 1024px) {
    .header, .header_not-main {
        ul {
            li {
                margin: 0; } } } }
@media all and (max-width: 770px) {
    .header, .header_not-main {
        ul {
            li {
                margin: 0; } } } }
@media all and (max-width: 600px) {
    .header, .header_not-main {
        width: 0;
        .hamb_left {
            filter: invert(0);
            left: calc(100vw - 70px); }
        .logo_mob {
            top: 75px;
            img {
                width: auto; } }
        ul {
            padding-top: 125px;
            li {
                font-size: 22px;
                margin: 5px 0;
                .bottom_line, .bottom_point {
                    display: none; }
                &:active, &:focus, &:hover {
                    background-color: transparent; }
                a {
                    &:active, &:focus, &:hover {
                        background-color: transparent;
                        color: #fff; } } } }
        .social {
            bottom: 25px;
            .social_icons {
                transform: scale(1);
                margin: 0 10px; } } }
    .header_not-main {
        .hamburger {
            filter: invert(1); }
        .hamb_left {
            filter: invert(1);
            left: calc(100vw - 70px); } } }
