.theme_black_back {
    background-color: #161616;
    .about__quote {
        background-color: #fff;
        p:before, p:after {
            filter: invert(0.6); } }
    .about__left__title p {
        color: #fff; }
    .about__left__social p {
        color: #fff; }
    .about__left__social__links a {
        filter: invert(1); }
    .news__title, .photo__title, .contacts__title, .video__title, .video__items__item__title, .press__title {
        p {
            color: #fff; }
        a {
            &:hover {
                color: #fff; } } }
    .grid {
        .grid-item {
            &:hover {
                img {
                    box-shadow: 0 24px 65px -20px rgba(255,255,255,.85); } } }
        .grid-item__date, .grid-item__title {
            p {
                color: #fff; } }
        .grid-item__read_more {
            p {
                color: #fff; }
            &__line {
                background-color: #fff;
                &:before, &:after {
                    background-color: #fff; } } } }
    .press__items__item__photo .owl-nav {
        .owl-prev, .owl-next {
            background-color: #fff; } }
    .press__items__item__desc {
        background-color: #fff;
        .press_prev_item, .press_next_item {
            background-color: #fff;
            a:hover {
                color: #161616; } }
        p:nth-child(3) {
            color: #161616; }
        .press_info {
            border-color: #161616; } }
    .contacts__form {
        background-color: #fff;
        input, textarea {
            background-color: #fff; }
        .submit_btn {
            background-color: #161616;
            border-color: #161616;
            color: #fff;
            &:hover {
                background-color: #fff;
                color: #161616; } } }
    .social {
        .social_icons {
            filter: invert(1); } }
    .nav {
        li.active {
            a {
                color: #fff; }
            .bottom_line, .bottom_point {
                background-color: #fff; } } }
    .photo_open__right {
        background-color: #fff !important; }
    .photo_open__nav {
        .nav_back, .nav_next {
            background-color: #fff; } }
    .logo_mob {
        img {
            filter: invert(0); } } }
.texture_black {
    background-image: url(../img/intro/texture.png) !important; }
.black_theme_menu {
    filter: invert(0) !important; }
@media all and (max-width: 1250px) {
    .theme_black_back {
        .nav {
            background-color: #161616; }
        .about__left__title p span {
            color: #fff; } } }
